/* app.css */

.container {
  flex: 1;
  padding: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.button {
  margin-bottom: 10px;
}

.list-item {
  padding: 10px;
  background-color: #f1f1f1;
  margin-top: 10px;  /* Daha fazla boşluk */
  margin-bottom: 10px; /* Daha fazla boşluk */
  border-radius: 5px;  /* Köşeleri yuvarla */
}

.input {
  border-width: 1px;
  margin-bottom: 10px;
  padding: 5px;
  width: 100%;
}

.message-box {
  border-width: 1px;
  padding: 10px;
  margin-bottom: 10px;
}

.chat-item {
  padding: 10px;
  background-color: #f1f1f1;
  margin-top: 5px;
  margin-bottom: 5px;
}

.chat-title {
  margin-bottom: 10px;
}
